.pagenotfound {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

.pagenotfound__title {
  margin: 246px 0 0 0;
  font-weight: 400;
  font-size: 140px;
  line-height: 169px;
  text-align: center;
}

.pagenotfound__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  margin-top: 5px;
}

.pagenotfound__link {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #ff6838;
  text-decoration: none;
  margin-top: 169px;
}

@media (max-width: 350px) {
  .pagenotfound__title {
    font-size: 80px;
    line-height: 97px;
    margin-top: 329px;
  }

  .pagenotfound__text {
    font-size: 12px;
    line-height: 15px;
    margin-top: 10px;
  }

  .pagenotfound__link {
    margin-top: 272px;
    font-size: 12px;
    line-height: 15px;
  }
}
