.header {
  margin: 18px 70px 18px 70px;
}

.header__container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.header__logo {
  width: 38px;
  height: 38px;
}

.header__nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.header__reg-link {
  margin-right: 30px;
  text-decoration: none;
  color: black;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
}

.header__reg-link:hover {
  opacity: 0.7;
  cursor: pointer;
}

.header__login-link {
  text-decoration: none;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: black;
  background-color: #2be080;
  border-radius: 3px;
  width: 76px;
  height: 24px;
  text-align: center;
  padding-top: 8px;
}

.header__user-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 0;
}

.header__user-nav {
  margin-left: 44px;
  display: flex;
  flex-direction: row;
  width: 100%;
}

.header__burger-menu {
  display: none;
  background-image: url("../../../images/burger-menu.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: transparent;
  border: none;
  padding: 0;
  width: 44px;
  height: 44px;
  position: absolute;
  top: 15px;
  right: 15px;
}

@media (max-width: 768px) {
  .header {
    margin: 18px 30px;
  }
  .header__user-nav {
    display: none;
  }

  .header__burger-menu {
    display: block;
  }
}

@media (max-width: 420px) {
  .header {
    margin: 20px 14px 17px 14px;
  }
  .header__nav {
    padding-top: 6px;
  }

  .header__logo {
    margin-top: 5px;
  }

  .header__reg-link {
    font-size: 10px;
    margin-right: 14px;
  }

  .header__login-link {
    font-size: 10px;
    padding-top: 5px;
    width: 54px;
    height: 21px;
  }

  .header__burger-menu {
    right: 15px;
    top: 26px;
    width: 37px;
    height: 40px;
  }
}
