.promo {
  margin: 19px 40px 0 40px;
  background: #f5f5f5;
  border-radius: 10px;
}

.promo__title {
  padding: 160px 0 0 0;
  max-width: 730px;
  margin: 0 auto;
  font-style: normal;
  font-weight: 400;
  font-size: 50px;
  line-height: 58px;
  text-align: center;
  letter-spacing: -0.04em;
}

@media (max-width: 768px) {
  .promo {
    margin: 20px 20px 0 20px;
  }

  .promo__title {
    font-size: 40px;
    line-height: 52px;
    max-width: 675px;
    padding: 350px 0 0 0;
  }
}

@media (max-width: 350px) {
  .promo {
    margin: 13px 14px 0 14px;
  }

  .promo__title {
    font-size: 29px;
    line-height: 37px;
    padding: 220px 0 0 0;
    max-width: 287px;
  }
}
